            async addNewSiteSettings(newSiteId){
                await this.getAllSettingsList();

                let nullArray=[
                '#7B68EE',
                '#ffffff',
                '#F3F3F3',
                '#000000',
                '#888888',
                '#ffffff',
                'right',
                'default'
