                    console.log(res);
                    alert('Ð¡Ð°Ð¹Ñ ÑÐ´Ð°Ð»ÐµÐ½');
                    this.getSites();

                }).catch((e)=>{
                    console.log(e);
                    alert('ÐÑÐ¸Ð±ÐºÐ° ÑÐ´Ð°Ð»ÐµÐ½Ð¸Ñ. ÐÐ¾Ð¿ÑÐ¾Ð±ÑÐ¹ÑÐµ Ð¿Ð¾Ð·Ð¶Ðµ')
                })
            }
        },

